import React, {Component} from 'react';
import './css/Game.css';

class Game extends Component {
    constructor(props){
        super(props);
        this.state = {
            redirect: false
        }
    }

    render(){
        let gameName = this.props.gamename;

        return(
        <div className="game-con" onClick={(e,gameName) => this.props.getStreamList(e,gameName)}>
            <img src={this.props.imgURL} alt="game-img"/>
            <h3>{this.props.gamename}</h3>
            <h4>Viewers: {this.props.viewers}</h4>
        </div>
        )
    }    
}

export default Game;


