import React, {Component} from 'react';
import App from './App';
import StreamList from './StreamList';
import ActualStream from './ActualStream';
import { Redirect } from 'react-router';


import {
    HashRouter,
    Route
} from 'react-router-dom';

class RouterApp extends Component {
    render(){
        if (this.props.channelURL !== undefined){
            console.log('channel if');
            return(
                <HashRouter>
                    <Route path="/streamlist" render={() =><ActualStream  channelURL={this.props.channelURL} />}/>
                </HashRouter>
            )
        }

        if (this.props.gameName !== undefined){
            console.log('streamlist if');
            console.log(this.props.gameName);
            return ( 
                <div className="stream-list--parent">      
                <HashRouter>
                {/* <div> */}
                    <Route path="/streamlist" render={() =><StreamList  gameName={this.props.gameName} />}/>
                {/* </div> */}
                </HashRouter >     
                <Redirect push to="/streamlist"/>
                </div>
            );} else {
                console.log('normal if');
                return(      
                    <HashRouter>
                        <div>
                            <Route path="/" render={()=><App />}/>
                            <Route path="/streamlist" render={() =><StreamList  gameName={this.props.gameName} />}/>
                        </div>
                    </HashRouter >
            );
            }
    }
}

export default RouterApp;  


